import {ButtonType} from "./button-type.model";
import {SegmentType} from "./segment-type";
import {VojCardHeaderModel} from "./voj-card-header.model";
import {VojMapModel} from "./voj-map.model";
import {TextType} from "./text-type";

export class VojCardModel {
    id : number;
    title : TextType;
    subtitles : Array<TextType>;
    cover : string;
    thumbnail : string;
    header : VojCardHeaderModel;
    socialButtons : Array<ButtonType>;
    menu : Array<ButtonType>;
    actions : Array<ButtonType>;
    segments : Array<SegmentType>;
    address : VojMapModel;

    constructor(){
        this.header = new VojCardHeaderModel();
    }
}


