import {VojCardModel} from "../models/voj-card.model";
import {TextType} from "../models/text-type";
import {VojMapModel} from "../models/voj-map.model";
import {VojCardHeaderModel} from "../models/voj-card-header.model";

export class SeachResultDTO {
    id : number;
    indexation : number;
    name : string;
    phone : string;
    email : string;
    availability : string;
    job : string;
    lat : string;
    lng : string;

    toVojCard() : VojCardModel {
        let result : VojCardModel = new VojCardModel();
        result.header = new VojCardHeaderModel();
        result.header.title = this.name;
        result.id = this.id;
        result.title = new TextType();
        result.title.label = this.name;
        result.thumbnail = "http://dev.vitonjob.com/resources/jobyer_avatar.png";
        result.cover = "http://dev.vitonjob.com/resources/726.jpg";
        result.subtitles = [
            {
                icon : '',
                label : this.job
            },
            {
                icon : '',
                label : 'Disponible ds maintenant'
            }
        ];
        result.actions = [{
            id : 'recruite',
            icon:'',
            label:'Recruter',
            action:()=> {
                console.log('REC');
            }
        },{
            id : 'details',
            icon:'',
            label:'Dtails',
            action:()=> {
                console.log('DTL');
            }
        }];

        result.socialButtons = [{
            id : 'mail',
            icon:'mail',
            label:'',
            action:()=> {
                console.log('SMS');
            }
        },{
            id : 'call',
            icon:'call',
            label:'',
            action:()=> {
                console.log('PHONE');
            }
        },{
            id : 'fav',
            icon:'star',
            label:'',
            action:()=> {
                console.log('FAV');
            }
        }];

        result.address = new VojMapModel();
        result.address.title = this.name;
        if(this.lat)
            result.address.lat = parseInt(this.lat);
        if(this.lng)
            result.address.lng = parseInt(this.lng);

        return result;
    }
}


