import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { AboutPage } from "../pages/about/about";
import {Configuration} from "../config/voj-configuration";
import {SearchPage} from "../pages/search/search";
import { TranslateService } from '@ngx-translate/core';
import {Page} from "ionic-angular/navigation/nav-util";
import {HomePage} from "../pages/home/home";
import {ContactPage} from "../pages/contact/contact";
import {NavigationService} from "../services/navigation.service";
import {envConfig} from "../config/environment";
import {VojConfiguration} from "../models/voj-configuration.model";
import {VojNavigation} from "../models/voj-navigation";
import {DetailsPage} from "../pages/details/details";
import {VojEventsProvider} from "../providers/voj-events/voj-events";

export interface PageInterface {
	title: string;
	name: string;
	component: any;
	icon: string;
	logsOut?: boolean;
	index?: number;
	tabName?: string;
	tabComponent?: any;
}

@Component({
	templateUrl: 'app.html'
})
export class MyApp {
  config: VojConfiguration;

  //contentList will be displayed in the left of the large screens
  @ViewChild('contentList') nav1: Nav;
  //contentDetails will be displayed in the center of the large screens
  @ViewChild('contentDetails') nav2: Nav;
  //contentMain will be displayed in the small screens and in the right for the large screens
  @ViewChild('contentMain') nav3: Nav;

  //the root page that will be shown in the contentMain nav
  rootPageMain:Page;
  //the root page that will be shown in the contentList nav
  rootPageList:Page = SearchPage;
  //the root page that will be shown in the contentDetails nav
  rootPageDetails:Page = DetailsPage;

  selectedTab: string = "searchTab";
  showList: boolean = true;
  showDetails: boolean = true;
  showMain: boolean = true;

  dataPageHeader:{
		title: string, 
		actions : [
			{ icon : string, label : string, action : Function },
			{ icon : string, label : string, action : Function },
			{ icon : string, label : string, action : Function }
		], 
		style : any
	}
	dataHeader:{ 
		menu : [
			{ 
				icon : string, 
				label : string, 
				action : Function 
			}
		],
		logo: string,
		searchbar: { 
			placeholder: string 
		}, 
		segments : [
			{ 
				title:	{ 
					icon : string, 
					label : string 
				}, 
				subtitle: { 
					icon : string, 
					label : string 
				}, 
				action: Function, 
				style : any 
			}
		] 
	};


	cards: any;
	listTitle: string;
	appPages: PageInterface[] = [
		{ title: 'Schedule', name: 'Tabs', component: TabsPage, tabComponent: "", index: 0, icon: 'calendar' },
		{ title: 'Speakers', name: 'Tabs', component: TabsPage, tabComponent: "", index: 1, icon: 'contacts' },
		{ title: 'Map', name: 'Tabs', component: TabsPage, tabComponent: "", index: 2, icon: 'map' },
		{ title: 'About', name: 'Tabs', component: TabsPage, tabComponent: "", index: 3, icon: 'information-circle' }
	];
	loggedInPages: PageInterface[] = [
		{ title: 'Account', name: 'AccountPage', component: "", icon: 'person' },
		{ title: 'Support', name: 'SupportPage', component: "", icon: 'help' },
		{ title: 'Logout', name: 'Tabs', component: TabsPage, icon: 'log-out', logsOut: true }
	];
	loggedOutPages: PageInterface[] = [
		{ title: 'Login', name: 'LoginPage', component: "", icon: 'log-in' },
		{ title: 'Support', name: 'SupportPage', component: "", icon: 'help' },
		{ title: 'Signup', name: 'SignupPage', component: "", icon: 'person-add' }
	];

    constructor(platform: Platform,
                statusBar: StatusBar,
                splashScreen: SplashScreen,
                private configurationService: Configuration,
                public event: Events,
                public navigationService: NavigationService,
                private translate: TranslateService, private vojEvent: VojEventsProvider) {

    this.config = this.configurationService.configuration;

    this.dataPageHeader = {
			title: 'string', 
			actions : [
				{ icon : 'search', label : 'button 1', action : Function },
				{ icon : 'search', label : 'button 2', action : Function },
				{ icon : 'search', label : 'button 3', action : Function }
			], 
			style : 'any'
		}

		this.dataHeader = { 
			menu : [
				{ 
					icon : 'search',
					label : 'Recherche',
					action : Function
				},
				{
					icon : 'search',
					label : 'Annonces',
					action : Function
				},
				{
					icon : 'search',
					label : 'Missions',
					action : Function
				},
				{
					icon : 'search',
					label : 'Compte',
					action : Function
				}
			],
			logo: 'assets/img/vitonjob.png',
			searchbar: {
				placeholder: 'string'
			},
			segments : [
				{
					title:	{
						icon : 'string',
						label : '2'
					},
					subtitle: {
						icon : 'string',
						label : 'Contrats en attente'
					},
					action: function(){
						console.log('works 1')
					},
					style : 'style2'
				},
				{
					title:	{
						icon : 'string',
						label : '1'
					},
					subtitle: {
						icon : 'string',
						label : 'Pointages  valider'
					},
                    action: function(){
                        console.log('works 2')
                    },
                    style : 'style2'
				},
				{ 
					title:	{ 
						icon : 'string', 
						label : '3' 
					}, 
					subtitle: { 
						icon : 'string', 
						label : 'Missions  clturer'
					},
                    action: function(){
                        console.log('works 3')
                    },
                    style : 'style2'
				}
			] 
		};

        platform.ready().then(() => {
            // Okay, so the platform is ready and our plugins are available.
            // Here you can do any higher level native things you might need.
            statusBar.styleDefault();
            splashScreen.hide();

      //determine the main root page according to the screen size
      this.config.isLargeScreen = (platform.width() >= envConfig.thresholdWidth);
		this.navigationService.hide('hide:details');
      this.getMainPage();
		});

        this.listTitle = "Ma liste des cartes";

        this.cards = [{
            title: "Jobyer 1",
            content: "contenu 1",
            actionName: "action 1"
        }, {
            title: "Jobyer 2",
            content: "contenu 2",
            actionName: "action 2"
        }, {
            title: "Jobyer 3",
            content: "contenu 3",
            actionName: "action 3"
        }, {
            title: "Jobyer 4",
            content: "contenu 4",
            actionName: "action 4"
        }, {
            title: "Jobyer 5",
            content: "contenu 5",
            actionName: "action 5"
        }];

        this.initializeTranslation();
    }

	ngOnInit() {
    this.configurationService.initialize();

    this.navigationService.initialize(this.nav1, this.nav2, this.nav3, this.showList, this.showDetails, this.showMain);

    this.event.subscribe('hide:list', () => {
      this.showList = false;
    });
    this.event.subscribe('hide:details', () => {
      this.showDetails = false;
    });
    this.event.subscribe('hide:main', () => {
      this.showMain = false;
    });

    this.event.subscribe('navigate:list', (data:VojNavigation) => {
      if(data.isRoot){
        this.nav1.setRoot(data.page, data.params);
        this.showList = true;
      }else{
        this.nav1.push(data.page, data.params);
        this.showList = true;
      }
    });

    this.event.subscribe('navigate:details', (data:VojNavigation) => {
      if(data.isRoot){
        this.nav2.setRoot(data.page, data.params);
        this.showDetails = true;
      }else{
        this.nav2.push(data.page, data.params);
        this.showDetails = true;
      }
    });

    this.event.subscribe('navigate:main', (data:VojNavigation) => {
      if(data.isRoot){
        this.nav3.setRoot(data.page, data.params);
        this.showMain = true;
      }else{
        this.nav3.push(data.page, data.params);
        this.showMain = true;
      }
    });

		this.event.subscribe('pop:list', () => {
			this.nav1.pop();
    });
    this.event.subscribe('pop:details', () => {
      this.nav2.pop();
    });
    this.event.subscribe('pop:main', () => {
      this.nav3.pop();
    });



    /*this.catchNavigationEvents('navigate:list', nav1, showList);
    this.catchNavigationEvents('navigate:details', nav2, showDetails);
    this.catchNavigationEvents('navigate:main', nav3, showMain);*/
  }

    openPage() {

    }

    openTutorial() {

    }

    isActive(page: PageInterface) {
        let childNav = this.nav1.getActiveChildNav();

        // Tabs are a special case because they have their own navigation
        if (childNav) {
            if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
                return 'primary';
            }
            return;
        }

        if (this.nav1.getActive() && this.nav1.getActive().name === page.name) {
            return 'primary';
        }
        return;
    }

	/*ionViewDidEnter(){
	 if (!this.platform.is('cordova')){

	 document.getElementsByClassName('tabbar show-tabbar')[0].style.visibility = 'hidden';
	 }
	 }*/

    initializeTranslation(): void {
        // Translation configuration
        this.translate.addLangs(['en', 'fr']);

        // Default language
        this.translate.setDefaultLang('en');

        // Use language
        this.translate.use('en');
    }

  /*
  Set the main page according to the screen width
   */
  getMainPage(){
    if(!this.config.isLargeScreen){
      console.log("just one pane");
      this.rootPageMain = SearchPage;
    }else{
      console.log("many panes");
      this.rootPageMain = AboutPage;
    }
  }

  tabChanged(e){
    switch(this.selectedTab){
      case("searchTab"):
        this.rootPageMain = SearchPage;
        break;
      case("advertTab"):
        this.rootPageMain = HomePage;
        break;
      case("missionTab"):
        this.rootPageMain = AboutPage;
        break;
      case("contactTab"):
        this.rootPageMain = ContactPage;
        break;
      default:
        this.rootPageMain = SearchPage;
        break;
    }
  }
}



