import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import {HttpRequestProvider} from "../http-request/http-request";
import {Configuration} from "../../config/voj-configuration";
import {VojTuple} from "../../models/voj-tuple.model";
import {SeachResultDTO} from "../../dtos/search-result.dto";


@Injectable()
export class SearchServiceProvider {

  idIndexation : number;

  constructor(public http: HttpRequestProvider,
              public configuration : Configuration) {

  }

  loadDetails(id : number){
    let payload = {
      "service": "DETAILS",
      "role": this.configuration.configuration.customer == "J" ? "jobyer" : "employer",
      "selectedRow": id,
      "index": this.idIndexation
    };
    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }

    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        console.log(JSON.stringify(data));
        resolve(data);
      });
    });
  }

  /**
   * Seek offers based on natural language query
   * @param fullText
   * @returns {Promise<T>}
   */
  semanticSearch(fullText : string) {
    let payload = {
      "service": "FULL_TEXT",
      "role": this.configuration.configuration.customer == "J" ? "jobyer" : "employer",
      "fullText": fullText
    };

    let url = "";
    for (let i = 0; i < this.configuration.configuration.urls.length; i++) {
      let t : VojTuple = this.configuration.configuration.urls[i];
      if (t.key == "search") {
        url = t.value;
        break;
      }
    }
    return new Promise(resolve => {
      this.http.call(payload, url, "", true).subscribe((data:any)=>{
        let cards = [];
        for(let i = 0 ; i < data.length ; i++) {
          this.idIndexation = data[i].indexation;
          let r = new SeachResultDTO();
          r.id = data[i].id;
          r.job = data[i].job;
          r.name = data[i].name;
          r.lat = data[i].lat;
          r.lng = data[i].lng;
          r.email = data[i].email;
          let c = r.toVojCard();
          cards.push(c);
        }
        resolve(cards);
      });
    });
  }

}



