import { Component, Input, OnInit } from '@angular/core';
import { NavController } from 'ionic-angular'
import { IonicPage, Platform } from 'ionic-angular';
import { VojMenu } from '../voj-menu/voj-menu';
import {VojConfiguration} from "../../models/voj-configuration.model";
import {Configuration} from "../../config/voj-configuration";
import {NavigationService} from "../../services/navigation.service";


@IonicPage()
@Component({
	selector: 'voj-page-header',
	templateUrl: 'voj-page-header.html',
})
export class VojPageHeader implements OnInit{
	@Input('data') data;
    showHeader: boolean = true;
    //isDesktop: boolean = true;
    canGoBack: boolean = false;
    config: VojConfiguration;

	constructor(
        public platform: Platform,
        public navCtrl: NavController,
				public navigationService: NavigationService,
				public configurationService: Configuration
	) {
		//console.log('this.navCtrl.canGoBack()', this.navCtrl.canGoBack())
		this.config = this.configurationService.configuration;
		this.canGoBack = this.navCtrl.canGoBack();
	}

	goToPreviousPage(){
		if(this.config.isLargeScreen){
			this.navigationService.hide("hide:details")
		}else {
      this.navigationService.goBack("pop:details");
    }
	}

	ngOnInit(){
    	  //this.platform.is('core') ? this.isDesktop=true : this.isDesktop=false;
        //this.navCtrl.canGoBack() ? this.canGoBack=true : this.canGoBack=false;
	}

}



