import { Component } from '@angular/core';
import {NavController, NavParams} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {VojEventsProvider} from "../../providers/voj-events/voj-events";
import {NavigationService} from "../../services/navigation.service";


@Component({
  selector: 'page-details',
  templateUrl: 'details.html',
})
export class DetailsPage {

  card : VojCardModel;


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private navigationService: NavigationService,
              public eventBus : VojEventsProvider) {
    this.card = new VojCardModel();
    this.card.id = 0;
    this.eventBus.subscribeDetailsPage(this);

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DetailsPage');
  }

  updateCard(c : VojCardModel) {
    this.card = c;

  }

  noData(){
    return !this.card || this.card.id==0;
  }

  hideDetailsView(){
    this.navigationService.hide('hide:details');
  }
}



