import {Component, OnInit} from '@angular/core';
import {Events, IonicPage, NavController} from 'ionic-angular';
import {VojCardHeaderModel} from "../../models/voj-card-header.model";
import {SearchHeaderData} from "../../models/search-header.model";
import {VojCardModel} from "../../models/voj-card.model";
import {SearchQuery} from "../../models/search-query.model";
import {SearchServiceProvider} from "../../providers/search-service/search-service";
import {VojEventsProvider} from "../../providers/voj-events/voj-events";
import {NavigationService} from "../../services/navigation.service";
import {DetailsPage} from "../details/details";
import {VojNavigation} from "../../models/voj-navigation";
import {Configuration} from "../../config/voj-configuration";
import {VojMapModel} from "../../models/voj-map.model";


@IonicPage()
@Component({
    selector: 'page-search',
    templateUrl: 'search.html',
})
export class SearchPage implements OnInit {

    //  Page header
    pageHeader: VojCardHeaderModel;
    searchHeaderData: SearchHeaderData;
    cards: Array<VojCardModel>;
    gmapView :boolean = false;


    constructor(public navCtrl: NavController, private eventBus : VojEventsProvider,
                private navigationService : NavigationService, private configuration : Configuration,
                public searchService: SearchServiceProvider, private event: Events) {

    }

    ngOnInit() {

        this.gmapView = false;

        //  Page header
        this.pageHeader = new VojCardHeaderModel();
        this.pageHeader.title = "Recherche";
        this.pageHeader.actions = [
            {
                id: 'map',
                icon: 'map',
                action: ()=>{
                    if(!this.configuration.configuration.isLargeScreen) {
                        this.gmapView = !this.gmapView;
                        this.event.publish('MAP_DATA', this.cards);
                    }

                },
                label: ''
            },
            {
                id: 'help',
                icon: 'help',
                action: ()=>{
                    console.log("Help");
                },
                label: ''
            }
        ];
        this.searchHeaderData = {
            placeholders: {
                main: 'Recherche libre',
                what: 'Job',
                where: 'Localisation',
                when: 'Crnaux de mission',
                who: 'Nom'
            },

            action: (q: SearchQuery) => {
                this.search(q);
            }
        };





    }

    search(q: SearchQuery) {

        if (q.queryType == 'full_text' && q.freeText && q.freeText !='') {

            //TODO TIM: Data should have an Array<VojCardType> as type
            this.searchService.semanticSearch(q.freeText).then((data: any) => {
                this.cards = data;
                for (let i = 0; i < this.cards.length; i++) {
                    let c = this.cards[i];
                    for (let j = 0; j < c.actions.length; j++) {
                        let action = c.actions[j];
                        if (action.id == "details") {
                            action.action = () => {
                                this.gotoDetails(c);
                            };
                            break;
                        }
                    }
                }
                //TODO TIM: an event service that defines an event for every data structure and then verify types of sent objects
                //TODO TIM: All maps will be updated. We should define witch map to update, if we have more than one..
                // sending values to map component in other pages
                this.eventBus.setMapData(this.cards);
            });

        }
    }

  gotoDetails(card : VojCardModel){
    console.log("Details : "+card.id);
    let vojNav: VojNavigation = {page: DetailsPage, isRoot : false};
    this.navigationService.navigate('navigate:details', vojNav);

    this.searchService.loadDetails(card.id).then((data:any)=>{
        let c = new VojCardModel();

        c.id = card.id;
        c.title = card.title;
        c.subtitles = [];
        c.cover = card.cover;
        c.thumbnail = card.thumbnail;
        c.header = card.header;
        c.socialButtons = card.socialButtons;
        c.menu = card.menu;
        c.actions = [{
            id : '',
            icon : '',
            label : 'Recruter',
            action : ()=>{

            }

        }];
        c.segments = card.segments;


        for(let i = 0 ; i < card.subtitles.length ; i++)
            c.subtitles.push(card.subtitles[i]);

        if(data.address)
            c.subtitles.push({
                label : data.address,
                icon : ''
            });
        if(data.languages && data.languages.length>0) {
            for(let i = 0 ; i<data.languages.length ; i++) {
                let lang = data.languages[i].label+ " " + (data.languages[i].level == 2?' courant ':' dbutant ');
                c.subtitles.push({
                    label : lang,
                    icon : ''
                });
            }
        }
        if(data.qualities && data.qualities.length>0) {
            for(let i = 0 ; i<data.qualities.length ; i++) {
                let qual = data.qualities[i].label;
                c.subtitles.push({
                    label : qual,
                    icon : ''
                });
            }
        }
        this.eventBus.publishCardDetails(c);

    });
  }



    ionViewDidLoad() {
        console.log('ionViewDidLoad SearchPage');
    }

}



