import {Injectable} from "@angular/core";
import {Events, Nav, Platform} from "ionic-angular";
import {Page} from "ionic-angular/navigation/nav-util";
import {VojNavigation} from "../models/voj-navigation";
import {VojConfiguration} from "../models/voj-configuration.model";
import {Configuration} from "../config/voj-configuration";

@Injectable()
export class NavigationService{
  config: VojConfiguration;

  constructor(public event: Events,
              public configurationService: Configuration){
    this.config = this.configurationService.configuration;
  }

  initialize(nav1: Nav, nav2: Nav, nav3: Nav, showList: boolean, showDetails: boolean, showMain: boolean){
    //Publish navigation events


    //hide the details nav
    //this.catchHideNavEvents('hide:details', showDetails);
  }

  navigate(navName: string, vojNav: VojNavigation){
    if(this.config.isLargeScreen){
      this.event.publish(navName, vojNav);
    }else{
      this.event.publish('navigate:main', vojNav);
    }
  }

  hide(eventName: string){
    if(this.config.isLargeScreen){
      this.event.publish(eventName);
    }
  }

  goBack(navName: string){
    if(this.config.isLargeScreen){
      this.event.publish(navName);
    }else{
      this.event.publish('pop:main');
    }
  }

  catchNavigationEvents(navName: string, nav: Nav, showNav: boolean){
    this.event.subscribe(navName, (data:VojNavigation) => {
        if(data.isRoot){
          nav.setRoot(data.page, data.params);
          showNav = true;
        }else{
          nav.push(data.page, data.params);
          showNav = true;
        }
    });
  }

  /*catchHideNavEvents(navName: string, nav: Nav){
    this.event.subscribe(navName, (showNav:boolean) => {

    });
  }*/
}


