import { Injectable } from '@angular/core';
import {Events} from "ionic-angular";
import {VojCardModel} from "../../models/voj-card.model";
import {VojNavigation} from "../../models/voj-navigation";
import {DetailsPage} from "../../pages/details/details";

/*
  Generated class for the VojEventsProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class VojEventsProvider {

  detailsCardPage : DetailsPage;

  constructor(private event:Events) {
    console.log('Hello VojEventsProvider Provider');
  }

    // MAP EVENTS
    /**
     * Send an event to set map values
     * @param {Array<VojCardModel>} data
     */
    setMapData(data: Array<VojCardModel>) {
        this.event.publish('MAP_DATA', data);
    }
    /**
     * Get an event to load map values
     */
    getMapData() {
        return new Promise((resolve) => {
            this.event.subscribe('MAP_DATA', (data: Array<VojCardModel>) => {
                resolve(data);
            });
        });
    }

  subscribeDetailsPage(instance : DetailsPage) {
    this.detailsCardPage = instance;
  }

  publishCardDetails(card : VojCardModel) {
    this.detailsCardPage.updateCard(card);
  }

  /*getMapData () {
    return this.event.subscribe('MAP_DATA', ()=> {return})
  }*/
    /**
     * Navigation via events
     * @param {string} topic
     * @param {VojNavigation} data
     */
    setMainNavigation(data: VojNavigation){
        this.event.publish('NAVIGATE_MAIN', data);
    }

    getMainNavigation() {
    }

    setDetailsView(data:{isVisible:boolean, id:number}){
        this.event.publish('SHOW_DETAILS', data);
    }

    getDetailsView(){
        return new Promise((resolve) => {
            this.event.subscribe('SHOW_DETAILS', (data: {isVisible:boolean, id:number}) => {
                resolve(data);
            });
        });
    }


    returnPromise(topic: string) {
        return new Promise((resolve) => {
            this.event.subscribe(topic, (data: Array<VojCardModel>) => {
                resolve(data);
            });
        });
    }

}



