import {Component, Input, ViewChild, ElementRef, OnInit} from '@angular/core';
import {Events, IonicPage, Platform} from 'ionic-angular';
import {VojCardModel} from "../../models/voj-card.model";
import {VojEventsProvider} from "../../providers/voj-events/voj-events";
import {SearchServiceProvider} from "../../providers/search-service/search-service";
import {VojNavigation} from "../../models/voj-navigation";
import {DetailsPage} from "../../pages/details/details";
import {NavigationService} from "../../services/navigation.service";

declare var google: any;

@IonicPage()

@Component({
    selector: 'voj-map',
    templateUrl: 'voj-map.html',
})

export class VojMap implements OnInit {
    @Input('data')
    set data(value: Array<VojCardModel>) {

        this.eventBus.getMapData().then((eventValue: any) => {
            if (eventValue) {
                this.mapData = eventValue;
                this.initMap();
            } else if (value) {
                this.mapData = value;
                this.initMap();
            }
        });
    }

    @ViewChild('map') mapElement: ElementRef;
    map: any;
    hiddenMap: boolean = true;
    mapData: Array<VojCardModel>;

    constructor(public platform: Platform,
                private searchService : SearchServiceProvider,
                private eventBus : VojEventsProvider,
                private navigationService : NavigationService,
                private event: Events) {

    }

    ngOnInit() {
        this.initMap();
    }

    initMap() {
        let latLng = new google.maps.LatLng(48.855168, 2.344813);
        this.loadJSMap(latLng);
        this.hiddenMap = false;
        if (this.mapData && this.mapData.length > 0) {

            for (let i = 0; i < this.mapData.length; i++) {
                let card: VojCardModel = this.mapData[i];
                this.addMarker(card);
            }
        }
    }

    addMarker(card: VojCardModel) {
        let marker = new google.maps.Marker({
            position : {lat : card.address.lat, lng : card.address.lng},
            map : this.map,
            icon : 'http://maps.google.com/mapfiles/ms/icons/green-dot.png',
            title : card.address.title
        });
        let infoWindow = new google.maps.InfoWindow({
            content: '<div id="myInfoWinDiv_'+card.id+'">' +
                '<img src="'+card.thumbnail+'" style="width:50px;height: 50px"><br />'+
            '<strong>'+card.title.label+'</strong><br />' +
                card.subtitles[0].label
            + '</div>'
        });
        google.maps.event.addListener(marker, 'click', function () {
            infoWindow.open(this.map, marker);
            google.maps.event.addDomListener(document.getElementById('myInfoWinDiv_'+card.id) , 'click', function () {
                this.updateDetails(card);
            }.bind(this));
        }.bind(this));



    }

    loadJSMap(latLng : any) {

        let mapOptions = {
            center: latLng,
            zoom: 9,
            mapTypeId: google.maps.MapTypeId.ROADMAP
        };
        this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
    }

    updateDetails(card:VojCardModel) {
        let vojNav: VojNavigation = {page: DetailsPage, isRoot : false};
        this.navigationService.navigate('navigate:details', vojNav);

        this.searchService.loadDetails(card.id).then((data:any)=>{
            let c = new VojCardModel();

            c.id = card.id;
            c.title = card.title;
            c.subtitles = [];
            c.cover = card.cover;
            c.thumbnail = card.thumbnail;
            c.header = card.header;
            c.socialButtons = card.socialButtons;
            c.menu = card.menu;
            c.actions = [{
                id : '',
                icon : '',
                label : 'Recruter',
                action : ()=>{

                }

            }];
            c.segments = card.segments;
            c.address = card.address;

            for(let i = 0 ; i < card.subtitles.length ; i++)
                c.subtitles.push(card.subtitles[i]);

            if(c.address)
                card.subtitles.push({
                    label : data.address,
                    icon : ''
                });
            if(data.languages && data.languages.length>0) {
                for(let i = 0 ; i<data.languages.length ; i++) {
                    let lang = data.languages[i].label+ " " + (data.languages[i].level == 2?' courant ':' dbutant ');
                    c.subtitles.push({
                        label : lang,
                        icon : ''
                    });
                }
            }
            if(data.qualities && data.qualities.length>0) {
                for(let i = 0 ; i<data.qualities.length ; i++) {
                    let qual = data.qualities[i].label;
                    c.subtitles.push({
                        label : qual,
                        icon : ''
                    });
                }
            }
            this.eventBus.publishCardDetails(c);
        });
    }

}


